export const toBeChinese = function (object) {
    console.log(object)
    let ChineseObject = {}
    keyMap.forEach((val, key) => {
        let temp = object[key]
        if (temp && val.name) {
            let selectable = val.require ? "" : "(可选)";
            ChineseObject[`${val.name}${selectable}`] = temp
        }
    })
    return ChineseObject
}