// 将格林威治时间转化为标准时间，type是布尔值,表示精度是否只精确到日
function timeTrans(time, type) {
  let date = new Date(new Date(time).getTime() + 8 * 3600 * 1000)
  date = date.toJSON()
  if (type) {
    date = date.substring(0, 10)
  } else {
    date = date.substring(0, 19).replace('T', ' ')
  }
  return date
}
const timeList = [
  '活动结束时间',
  '活动开始时间',
  '创建时间',
  '产品过期时间',
  '产品售卖时间'
]
const PriceList = ['划线价格', '未划线价格']
const bolList = ['是否优惠', '是否开启二次审核']
const FoodMap = new Map([
  [1, '自制餐饮'],
  [2, '外部采购'],
  [3, '不提供餐饮']
])
const RefundPolicyMap = new Map([
  [1, '无条件退'],
  [2, '不可退'],
  [3, '有条件退-方案1'],
  [4, '有条件退-方案2'],
  [5, '有条件退-方案3'],
  [6, '有条件退-方案4']
])
const categoriesMap = new Map([
  [1, '体育'],
  [3, '机械'],
  [4, '手工'],
  [5, '地理'],
  [6, '医疗'],
  [7, '军事'],
  [8, '天文'],
  [9, '自然'],
  [10, '户外'],
  [11, '艺术'],
  [12, '人文'],
  [13, '计算机'],
  [14, '商业'],
  [15, '其他'],
  [16, '亲子']
])

const needBeItem = ['活动语言', '产品特色']
export const toBeRow = function (complexObject) {
  console.log(complexObject)
  let RowObject = {}
  for (let item in complexObject) {
    // 空值处理
    let value = complexObject[item]
    if (Array.isArray(value)) {
      const list = complexObject[item]
      if (list.length == 0) {
        RowObject[item] = '无'
        continue
      }
    }
    if (value === '') {
      RowObject[item] = '无'
      continue
    }
    // 对时间进行处理
    if (timeList.includes(item)) {
      RowObject[item] = timeTrans(value, 1)
      continue
    }
    // 处理活动时间地点
    if (item == '活动时间地点') {
      RowObject[item] = `开始时间：${timeTrans(value.start_time, 1)} 
结束时间：${timeTrans(value.end_time, 1)} 
开始地点：${value.start_location} ${value.start_location_detailed} 
结束地点：${value.end_location} ${value.end_location_detailed} 
`
      continue
    }
    if (item == '优势认证(可选)') {
      let rowStr = ''
      value.forEach(element => {
        let str = `${element.title} : ${element.detail} \n`
        rowStr += str
      })
      RowObject[item] = rowStr
      continue
    }
    if (item == '活动售卖时间') {
      RowObject[item] = `起售时间：${value.start_time} 
停售时间：${value.end_time} 
`
      // `起售时间：${timeTrans(value.start_time,1)}
      // 停售时间：${timeTrans(value.end_time,1)}
      // `
      continue
    }
    if (item == '服装要求') {
      if (value.unified) {
        RowObject[item] = value.detail
      } else {
        RowObject[item] = '无服装要求'
      }
      continue
    }
    if (item == '日程安排') {
      let rowStr = ''
      value.forEach(day => {
        let oneDaystr = `日期：${timeTrans(day.date, true)} \n`
        let dayStr = ''
        day.content.forEach(itemTime => {
          let strItem = `${timeTrans(itemTime.from, false).substring(
            10,
            16
          )}——${timeTrans(itemTime.to, false).substring(10, 16)}:${
            itemTime.detail
          } \n`
          dayStr += strItem
        })
        oneDaystr += dayStr
        rowStr += oneDaystr
      })
      RowObject[item] = rowStr
      continue
    }
    if (item == '紧急联系人') {
      let rowStr = ''
      if (value && value.forEach) {
        value.forEach(people => {
          let itemStr = `姓名：${people.name}  职务：${people.title} 电话：${people.phone} \n`
          if (people.detail) {
            itemStr += `    备注：${people.detail}`
          }
          rowStr += itemStr
        })
      }
      RowObject[item] = rowStr
      continue
    }
    if (item == '餐饮条件(可选)') {
      const FoodType = FoodMap.get(Number(value.type))
      let str = `餐饮方式:${FoodType} \n备注：${value.description}`
      RowObject[item] = str
      continue
    }
    if (item == '是否限制活动人数') {
      if (value) {
        RowObject[item] = '限制活动人数'
      } else {
        RowObject[item] = '不限制活动人数'
      }
      continue
    }
    if (needBeItem.includes(item)) {
      RowObject[item] = value.join('、')
      continue
    }
    if (item == '师生比') {
      RowObject[item] = `${value[0]}:${value[1]}(老师：学生)`
      continue
    }
    if (PriceList.includes(item)) {
      RowObject[item] = `${String(value).slice(0, -2)}.${String(value).slice(
        -2
      )}元`
      continue
    }
    if (item == '退改政策') {
      RowObject[item] = RefundPolicyMap.get(value)
      continue
    }
    if (item == '商品类别(可选)') {
      let list = []
      value.forEach(item => {
        item.category && list.push(categoriesMap.get(item.category))
      })
      RowObject[item] = list.join('、')
      continue
    }
    if (bolList.includes(item)) {
      if (Boolean(value)) {
        RowObject[item] = '是'
      } else {
        RowObject[item] = '否'
      }
      continue
    }
    if (item == '专业拍摄') {
      if (value.needed) {
        RowObject[item] = `有${value.size}个拍摄人员使用${value.device}拍摄`
      } else {
        RowObject[item] = '无专职拍摄人员'
      }
      continue
    }
    RowObject[item] = value
  }
  return RowObject
}
